body {
  margin: 0;
  padding: 0;
  position: relative;
  font-family: 'Open Sans', sans-serif;
}

.section-heading {
  font-family: 'Raleway', sans-serif;
  border-style: solid;
  border-width: 1px;
  padding: 20px 0;
  font-size: 1.5em;
  text-align: center;
  border-left: none;
  border-right: none;
}

.section-content {
  padding: 70px 0;
  max-width: 750px;
  margin: 0 auto;
}

.section-content-heading {
  font-family: 'Raleway', sans-serif;
  font-size: 2em;
  margin-bottom: 30px;
}

.navbar-default {
  background: #78909c;
  border-color: #78909c;
  margin: 0;
}

.navbar-default .navbar-nav > li > a {
  color: #fafafa;
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  font-size: 0.9em;
  padding-left: 30px;
  padding-right: 30px;
  text-transform: uppercase;
}

.navbar-default .navbar-nav > .active > a {
  color: inherit;
  background-color: inherit;
}

.navbar-default .navbar-toggle .icon-bar {
  background-color: #ddd;
}

.navbar-default .navbar-toggle:focus, .navbar-default .navbar-toggle:hover {
  background-color: inherit;
  opacity: 0.6;
}

.navbar-default .navbar-nav > li > a:hover {
  color: #fafafa;
  background: #78909c;
}

.navbar-nav li a {
    line-height: 70px;
    height: 70px;
    padding: 0;
}

.scroll-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  cursor: pointer;
  padding: 0 5px;
  color: rgba(0, 0, 0, 0.4);
  font-weight: bold;
}

.scroll-top:hover {
  color: rgba(0, 0, 0, 0.4);
}
.scroll-top:focus {
  outline: none;
  color: inherit;
}

.home {
  text-align: center;
  margin-bottom: 100px;
  margin-top: 30px;
}

.home > .name {
  font-family: 'Rancho', sans-serif;
  font-size: 5em;
  margin: 0 auto;
}

.home > .description {
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 1.2em;
}


.project-container {
  padding: 20px 0 30px 0;
  cursor: pointer;
}

.project-container {
  border-top: 1px solid #ddd;
}

.project-align {
  margin: 0 auto;
  max-width: 750px;
}

.project-name {
  font-family: 'Raleway', sans-serif;
  font-size: 1.7em;
}

.project-description {
  margin-bottom: 20px;
  white-space: pre-wrap;
}

.project-image-container {
  padding: 5px;
}

.project-image {
  width: 100%;
  height: 150px;
  background-size: cover;
  background-position: center;
}

.project-collapse {
  padding-bottom: 30px;
}
.project-link {
  margin-top: 5px;
}

.tags {
  margin-top: 5px;
}

.tag {
  display: inline-block;
  margin-right: 5px;
  background: #607d8b;
  color: #fff;
  padding: 2px 5px 0.5px 5px;
  text-transform: uppercase;
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  font-size: 0.80em;
}

.contact-icon {
  height: 45px;
  width: 45px;
  padding: 5px;
  font-size: 1.5em !important;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  margin-top: 15px;
  margin-right: 15px;
  display: -ms-inline-flexbox !important;
  display: inline-flex !important;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.contact-link {
  color: inherit;
}

.contact-link:hover {
  color: inherit;
}

.gutter-left-sm {
  margin-left: 5px;
}

.gutter-x-20 {
  margin: 20px 0;
}

@media (min-width: 768px) {
  .navbar .navbar-nav {
    display: inline-block;
    float: none;
  }

  .navbar .navbar-collapse {
    text-align: center;
  }

  .container-fluid {
    padding: 0;
  }
}

.my-link {color: white; text-decoration: underline;} .my-link:hover{color: white;}


